#index01 {

	margin-top: -60px;

	.block {

		position: relative;
		overflow: hidden;

		margin: 10px 0;

		@include sm {

			width: calc(50% - 40px);

			margin: 10px 20px; }

		@include md {

			width: calc(25% - 40px);

			margin: 20px; }

		.img {

			height: 160px;
			border: 4px solid white;

			background-repeat: no-repeat;
			background-size: cover;
			background-position: center; }

		.hidden {

			position: absolute;
			top: 100%;
			left: 0;

			width: 100%;
			height: 100%;

			text-align: center;

			background: rgba(255, 255, 255, .5);

			transition: $transition;

			display: flex;
			flex-direction: column;
			justify-content: center;
			text-align: center;

			h2 {

				font-weight: 700;
				font-size: 19px;
				color: #333;

				&:after {

					content: "";

					display: block;
					width: 100px;
					height: 4px;

					margin: 8px auto 0;

					background: $primary-colour; } } }

		&:hover .hidden {

			top: 0; } } }

#index02 {

	margin-top: 100px;
	text-align: center;

	.arrow {

		display: none;

		position: absolute;
		top: 50%;

		width: 22px;
		height: 22px;

		background-position: center;
		background-size: contain;
		background-repeat: no-repeat;

		cursor: pointer;

		@include md {

			width: 32px;
			height: 32px;

			display: block; }

		&.left {

			left: -30px;
			background-image: url("../img/static/arrow-slider-left.png");

			@include md {

				left: -60px; } }

		&.right {

			right: -30px;
			background-image: url("../img/static/arrow-slider-right.png");

			@include md {

				right: -60px; } } }



	h2 {

		font-size: 24px; }

	.slider {

		background-image: url("../img/index/02-bg.jpg");
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;

		@include md {

			height: 480px; }

		.confinement {

			height: 100%;
			position: relative;

			.slider-body {

				height: 100%;

				padding: 60px 0;

				.slick-list {

					height: 100%;

					.slick-track {

						height: 100%;

						.slide {

							width: 100%;
							height: 100%;

							color: white;

							.block {

								display: flex;
								flex-direction: column;
								justify-content: center;

								text-align: center;

								@include md {

									height: 100%; }

								&.heading {

									color: $primary-colour;
									font-size: 28px;

									margin-bottom: 30px;

									@include md {

										margin-bottom: 0; } }

								.image {

									width: 70%;
									margin: auto;

									img {

										max-width: 100%; } }

								.text {

									font-size: 18px;
									line-height: 24px;
									margin-top: 30px;

									@include md {

										text-align: left;

										padding-left: 30px;
										margin-top: 0; }

									button {

										float: none;
										clear: none;
										display: inline-block;
										width: 150px;
										margin-top: 20px; } } } } } }

				.slick-dots {

					display: none;

					position: relative;
					top: 40px;

					@include md {

						display: block; }

					li {

						display: inline-block;

						&.slick-active {

							button {

								background: $primary-colour; } }

						button {

							font-size: 0;

							padding: 3px;
							margin: 8px;
							border-radius: 10px;

							background: silver; } } } } } } }
