footer {

	padding: 40px 30px;
	margin-top: 100px;

	background: #464646;
	color: white;

	a {

		color: white; }

	h3 {

		font-size: 17px;
		font-weight: 700;

		&:after {

			content: "";
			background: $primary-colour;

			display: block;

			width: 120px;
			height: 3px;

			margin: 26px 0 18px; } }

	ul {

		li {

			margin: 6px 0;

			a {

				font-size: 14px; } } } }
