//	Media Queries
//	Compiles in the same order as declared here by the combiner plugin.

$screen-small: 640px;
$screen-medium: 900px;
$screen-large: 1200px;

// <--  0       mobile     639  -->
// <--  640     small      899  -->
// <--  900     medium     1199  ->
// <--  1200+   large

@mixin sm {
	@media (min-width: $screen-small) {
		@content; } }

@mixin md {
	@media (min-width: $screen-medium) {
		@content; } }

@mixin lg {
	@media (min-width: $screen-large) {
		@content; } }

@mixin mbMax {
	@media (max-width: ($screen-small - 1)) {
		@content; } }

@mixin smMax {
	@media (max-width: ($screen-medium - 1)) {
		@content; } }

@mixin mdMax {
	@media (max-width: ($screen-large - 1)) {
		@content; } }



// Default Styles

$primary-colour: #f6a200;
$primary-colour-highlighted: #f6a200;
$primary-text-colour: #555;
$primary-text-accented-colour: black;
$primary-text-colour-reversed: white;
$primary-font-family: "Open Sans", Arial, Helvetica, sans-serif;
$primary-font-size: 16px;
$primary-font-weight: 400;
$primary-line-height: 1.25em;
$transition: .35s all ease-in-out;
